___CSS_LOADER_EXPORT___.push([module.id, `/* Main Footer Container */
.footer-container {
    background-color: #111; /* Dark background */
    color: #fff;            /* Light text */
    padding: 3rem 1.2rem 2rem 1.2rem;
    font-family: Arial, sans-serif;
  }
  
  /* ========================
     FANCY BRAND ROW
     ======================== */
  .fancy-brand-row {
    display: flex;
    align-items: center;
    gap: 1rem;
    margin-bottom: 1rem;
  }
  
  .fancy-brand-logo {
    max-width: 70px;
    height: auto;
    border-radius: 0px;
  }
  
  .fancy-text-group {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  
  .fancy-brand-title {
    margin: 0;
    font-size: 1.4rem;
    font-weight: 700;
    font-family: math;
    letter-spacing: 1px;
    text-transform: uppercase;
  }
  
  .fancy-brand-subtitle {
    margin: 0;
    font-size: 1rem;
    font-weight: 400;
    letter-spacing: 0.5px;
    opacity: 0.8;
  }
  
  /* ========================
     TAGLINE BELOW BRAND
     ======================== */
  .footer-tagline-container {
    text-align: center;
    margin-bottom: 2rem;
  }
  
  .fancy-brand-slogan {
    margin: 0;
    font-size: 0.9rem;
    opacity: 0.9;
    text-align: left;
    line-height: 1.4;
  }
  
  /* ========================
     SOCIAL ICONS ROW
     ======================== */

   .footer-social-icons {
    display: flex;
    justify-content: flex-start;
    margin-top: 1rem;
  }
  
  /* Circle for the icon with Instagram gradient */
  .social-circle {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    width: 40px;
    height: 40px;
    background: linear-gradient(45deg, #405de6, #5851db, #833ab4, #c13584, #e1306c, #fd1d1d);
    border-radius: 50%;
    margin: 0 0;
    padding: 0 0;
    transition: transform 0.2s ease;
  }
  
  .social-circle:hover {
    transform: scale(1.05);
  }
  
  /* The actual icon image inside the circle */
  .social-icon-img {
    width: 24px;
    height: 24px;
    opacity: 0.9;
  }
  
  
  /* ========================
     FOOTER SECTIONS
     ======================== */
  .footer-section {
    margin-bottom: 2rem;
  }
  
  .footer-section h3 {
    font-size: 1rem;
    margin: 2rem 0;
  }
  
  .footer-section ul {
    list-style: none;
    padding: 0;
    margin: 0;
  }
  
  .footer-section ul li {
    margin: 1rem 0;
    opacity: 0.8;
  }
  
  .footer-section ul li a {
    color: #fff;
    font-size: 0.8rem;
    text-decoration: underline;
    text-decoration-color: #5b5bed;
  }
  
  .footer-section ul li a:hover {
    text-decoration: underline;
  }
  
  .footer-section ul li:active {
    opacity: 100;
  }
  
  /* Additional styling for the address list */
  .address-list li {
    margin-bottom: 0.75rem;
    display: flex;
    align-items: center;
  }
  
  .map-icon {
    width: 16px;
    height: auto;
    margin-right: 0.5rem;
    vertical-align: text-bottom;
  }
  
  /* Updated address link styling with blue underline */
  .address-list li a {
    color: #fff;
    text-decoration: underline;
    text-decoration-color: #5b5bed;
    font-size: 0.8rem;
  }
  
  .address-list li a:hover {
    text-decoration-color: lightblue;
  }
  
/* Book Appointment Button */
.book-appointment-link {
    display: inline-block;
    color: #fff;
    text-decoration: none;
    background-color: rgb(49, 71, 69);
    padding: 0.75rem 1.5rem;
    border-radius: 5px;
    font-weight: bold;
    font-size: 0.9rem;
    transition: background-color 0.2s ease;
  }
  
  .book-appointment-link:hover {
    background-color: rgb(19, 223, 202);
  }
  
  .book-appointment-p {
    margin: 2rem 0;
    text-align: center;
  }
  
  
  /* Disclaimer Text */
  .footer-disclaimer-text {
    font-size: 0.8rem;
    opacity: 0.8;
    margin-top: 1rem;
    line-height: 1.4;
  }
  
  /* ========================
     BOTTOM BAR
     ======================== */
  .footer-bottom {
    border-top: 1px solid #333;
    padding-top: 1rem;
    text-align: center;
    font-size: 0.75rem;
    opacity: 0.8;
  }
  
  .footer-bottom-links {
    list-style: none;
    display: inline-flex;
    gap: 1rem;
    padding: 0;
    margin: 0.5rem 0;
    text-decoration: underline;
    text-decoration-color: #5b5bed;
  }
  
  .footer-bottom-links li a {
    color: #fff;
    text-decoration: none;
  }
  
  .footer-bottom-links li a:hover {
    text-decoration: underline;
  }
  
  .footer-credit {
    font-size: 0.75rem;
    opacity: 0.8;
  }
